@import url(reset.css);
* {
  text-decoration: none;
}

.container {
  max-width: 900px;
  height: auto;
  margin: 0 auto;
  box-sizing: border-box;
}

@media (max-width: 768px) {
  .container {
    box-sizing: border-box;
    width: 100%;
    padding: 20px;
    margin: 0;
  }
}

.nav__list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-bottom: 100px;
  margin-top: 20px;
}

.nav__item {
  font-family: "Oxygen", sans-serif;
  color: #41588c;
  font-size: 20px;
  font-style: bold;
}

.nav__item:hover {
  background-color: #41588c;
  color: white;
}

.nav__item--active {
  background-color: #41588c;
  color: white;
  font-family: "Oxygen", sans-serif;
  font-size: 20px;
  font-style: bold;
}

.nav__item--active:hover {
  background-color: #41588c;
  color: white;
}

.icon {
  width: 38px;
}

.header__icon {
  width: 38px;
  margin-left: 0;
}

.title {
  background-color: #41588c;
  font-size: 20px;
  font-family: "Oxygen", sans-serif;
  color: white;
  font-weight: bold;
}

.header {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: center;
}

.header-wrapper {
  margin-bottom: 80px;
}

.header__description {
  width: 50%;
  line-height: 2;
  margin-left: 40px;
  font-size: 20px;
  font-family: "Oxygen", sans-serif;
  color: #41588c;
}

@media (max-width: 768px) {
  .nav__list {
    margin-bottom: 0;
  }
  .header {
    flex-direction: column;
  }
  .header__description {
    width: 100%;
    margin-top: 40px;
    margin-left: 0;
  }
  .photo {
    margin: 0 auto;
  }
}

.photo {
  display: flex;
  justify-content: flex-start;
  box-sizing: border-box;
  max-width: 300px;
  height: auto;
  height: 100%;
}

.photo img {
  border-radius: 50%;
  width: 100%;
}

.skills {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
}

.about {
  width: 100%;
}

.about__skills {
  font-size: 20px;
  font-family: "Oxygen", sans-serif;
  color: #41588c;
}

.about__item, .contacts__item {
  margin-bottom: 20px;
  line-height: 2;
}

.about__description {
  margin-left: 20px;
}

@media (max-width: 576px) {
  .about__item, .contacts__item {
    flex-direction: column;
    text-align: center;
  }
}

.portfolio {
  width: 100%;
  box-sizing: border-box;
}

.portfolio__title {
  font-size: 20px;
  font-family: "Oxygen", sans-serif;
  color: #41588c;
  text-align: center;
  margin-bottom: 40px;
}

.portfolio__card {
  background-color: #41588c;
  height: 40%;
  padding: 30px 40px;
  display: flex;
  flex-direction: row;
  border-bottom: 10px solid white;
}

.portfolio__img {
  border-radius: 50px;
  width: 40%;
  margin-right: 70px;
  transition: transform .5s ease-in;
  position: relative;
}

.portfolio__img:hover {
  cursor: pointer;
}

.portfolio__desc {
  font-size: 20px;
  font-family: "Oxygen", sans-serif;
  color: white;
}

.contacts {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 40px;
  font-size: 20px;
  font-family: "Oxygen", sans-serif;
  color: #41588c;
}

.contacts__header {
  text-align: start;
}

.contacts__item {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 20px;
}

.contacts__item span {
  display: block;
}

@media (max-width: 768px) {
  .portfolio__card {
    flex-direction: column;
    box-sizing: border-box;
  }
  .portfolio__desc {
    margin-top: 20px;
  }
  .portfolio__img {
    width: 100%;
    margin: 0 auto;
  }
  .about__item, .contacts__item {
    flex-direction: column;
    text-align: center;
  }
}
